@import '../../variables.scss';
@import '../../mixin.scss';

.tabStyle {
	:global {
		.nav-pills {
			width: 100%;
			background-color: #EFEFF2;
			padding: 4px;
			border-radius: 20px;

			.nav-item {
				flex-grow: 1;
				text-align: center;
				text-transform: capitalize;

				.nav-link {
					color: $Grey;
					font-size: 14px;
					font-weight: 400;

					&:hover {
						cursor: pointer;
						color: $Black;
					}

					&.active {
						background-color: $PureWhite;
						color: $DarkBlack;
						font-weight: 500;
						border-radius: 16px;
					}
				}
			}
		}
	}
}

.lockLayout {
	.title {
		background-color: black;
		padding: 2em;

		h1 {
			@include Barlow;
			font-size: 36px;
			color: $PureWhite;
			text-align: center;
			text-transform: capitalize;
		}

		.description {
			width: 100%;
			@include Barlow;
			font-size: 16px;
			color: $PureWhite;
			font-weight: 400;
			text-align: center;
		}
	}

	.intro {
		background-color: black;
		padding: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		color: white;

		.introContent {
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: row;
			width: fit-content;

			.leftPane {
				display: flex;
				flex-direction: column;
				justify-content: flex-start;
				align-items: flex-start;

				.line {
					width: 40px;
					height: 8px;
					background-color: white;
				}

				.introTitle {
					@include Barlow;
					font-weight: 800;
					font-size: 52px;
					line-height: 1;
				}

				.introDescription {
					@include Barlow;
					font-weight: 400;
					font-size: 20px;
					letter-spacing: 0.1em;
				}

				button {
					@include Barlow;
					background: #00D092;
					border-radius: 24px;
					padding: 13px 2em;
					color: white;
					text-transform: capitalize;
					font-weight: 500;
					font-size: 18px;
					border: none;
					cursor: pointer;
				}
			}
		}
	}

	.content {
		background: linear-gradient(900deg, black 150px, #F5F5F5 0%);
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: flex-start;
		margin-bottom: 3em;

		.column {
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: flex-start;
			margin-left: 0.5em;

			.faq {
				margin-top: 1em;
				display: flex;
				flex-direction: column;
				justify-content: flex-start;
				align-items: flex-start;
				padding: 1.5em 0.5em;
				width: 100%;

				h2 {
					@include Barlow;
					font-weight: 700;
					font-size: 28px;
					color: #040A2D;
				}

				ol {
					margin: 0;
					margin-top: 1em;
					padding: 0;
					width: 100%;
				}
			}
		}
	}
}

.panel {
	border-radius: 16px;
	background-color: white;
	padding: 2em;
	width: 590px;
	margin: 0 0.5em;

	.tabPane {
		padding: 1em 0;

		.keyAndValue {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			text-transform: capitalize;

			.key {
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}

		.assetsLabel {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			text-transform: capitalize;
			background: #EFEFF2;
			border: 1px solid #DBDBE6;
			border-radius: 12px;
			padding: 1em;
		}

		button {
			@include Barlow;
			width: 100%;
			background: #00D092;
			border-radius: 24px;
			padding: 13px 0;
			color: white;
			text-transform: capitalize;
			font-weight: 500;
			font-size: 18px;
			border: none;
			cursor: pointer;

			&:disabled {
				filter: grayscale(50%);
				pointer-events: none;
				cursor: not-allowed;
			}
		}

		.buttonBordred {
			border: 1px solid #00D092;
			border-radius: 24px;
			padding: 13px 2em;
			width: fit-content;
			background-color: white;
			color: black;
			font-weight: 500;
			font-size: 18px;
			color: #00D092;

			&:disabled {
				filter: grayscale(100%);
			}
		}
	}
}

.scoreLabelLayout {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;

	img {
		width: 48px;
	}

	.info {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		margin-left: 1em;
		min-height: 54px;

		.infoTitle {
			@include Barlow;
			font-weight: 400;
			font-size: 14px;
			color: #494E67;
			margin-bottom: 0.5em
		}

		.infoValue {
			@include Barlow;
			font-weight: 700;
			font-size: 28px;
			color: #040A2D;
			margin-bottom: 0.2em;
			text-transform: capitalize
		}
	}
}

.scoreDescription {
	@include Barlow;
	font-weight: 400;
	font-size: 14px;
}

.expandableLabelLayout {
	width: 100%;
	border-radius: 16px;
	background-color: white;
	margin: 0;
	padding: 1em;
	margin-bottom: 1em;

	.label {
		@include Barlow;
		font-size: 18px;
		color: #040A2D;
		width: 100%;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		cursor: pointer;

		.text {
			width: 90%;
		}

		.icon {
			color: #00D092;
			font-size: 18px;
		}
	}

	.expandableContent {
		margin: 1em 0 0 0;
	}
}

.poolListItem {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	width: 100%;

	.poolListItemLeftLabel {
		display: flex;
		flex-direction: row;

		.poolListItemTitle {
			@include Barlow;
			font-weight: 500;
			font-size: 16px;
			color: #040A2D;
		}
	}

	.poolListItemRightLabel {
		width: fit-content;
	}
}

.lockModal .modal-dialog .modal-content {
	height: fit-content;
}

.modalTitle {
	@include Barlow;
	font-weight: 500;
	font-size: 16px;
	color: #040A2D;
	text-align: left;
	width: 100%;
}

.modalBodyCenter {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-top: 40px;
}

.modalBody {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;

	.key {
		@include Barlow;
		font-weight: 400;
		font-size: 14px;
		color: #040A2D;
		text-transform: capitalize;
		margin: 0.5em 0
	}

	.value {
		@include Barlow;
		font-weight: 700;
		font-size: 20px;
		color: #00D092;
	}
}

.modalButtons {
	margin-top: 2em;
	width: 100%;

	button {
		@include Barlow;
		width: 100%;
		background: #00D092;
		border-radius: 24px;
		padding: 10px 0;
		color: white;
		text-transform: capitalize;
		font-weight: 500;
		font-size: 18px;
		border: none;
		cursor: pointer;

		&:disabled {
			filter: grayscale(50%);
			pointer-events: none;
			cursor: not-allowed;
		}
	}
}