@import '../../variables.scss';
@import '../../mixin.scss';

.expandableListLayout {
	border: 1px solid #DBDBE6;
	border-radius: 12px;
	padding: 1.5em;
	background-color: white;
	width: 100%;
	height: fit-content;
	padding: 0.5em 1em;
	display: flex;
	flex-direction: row;

	div {
		width: 100%;
		margin: 0.5em 0;
	}

	.expandBar {
		text-align: center;
		@include Barlow;
		font-weight: 500;
		font-size: 14px;
		color: #00D092;
	}
}